


var andonIndexView = {
	timer : null,
	init: function() {
		util.setBreadcrumb(["质量Andon"]);
		this.loadPage();
	},
	event: function() {
		var _this = this;
			$.each($("a[type='andon']"),function(index,item){
				var el = $(item);
				if(el.attr("statusECode") == 2){//触发状态
					el.attr("statusECode") == 2;
					if(Number(el.attr("touchTimeLong")) <= 10000){
						el.css("background","#FFFF00");//黄
					}
					if(Number(el.attr("touchTimeLong")) > 10000){
						el.css("background","#FF0000");//红
					}
				}else{//未触发
					el.css("background","#00FF00");
					el.attr("statusECode") == 1;
				}
			}),

			$("a[type='andon']").on("click",function(e){
				var __this = _this;
				var el = $(e.target);
				if(e.target.tagName != "A"){
					el = el.parents("a");
				}
				//更改数据库andon状态
				app.request({
					url: 'restful/service/execute',
					type:'POST',
					data:{
						"serviceCode": "ANDON.UPDATE",
						"siteCode" : app.siteCode,
						"andonCode" : el.attr("andonCode"),
						"workUnitCode" : el.attr("workUnitCode"),
						"modifyUser" : el.attr("modifyUser")
						// "templateCode" : el.attr("templateCode")
					},
					success: function (data) {
						//更新前端样式
						if(el.attr("statusECode") == 2){//当前为异常
							el.css("background","#00FF00")//绿
							el.attr("statusECode",1);
							el.attr("touchTimeLong",0);
							el.find(".touch-time").text("");
						}else{//当前为正常
							__this.calTime(new Date());
							el.css("background","#FFFF00");//黄
							el.attr("statusECode",2);
						}
					},
					error: function () {
						myApp.alert("语言列表加载失败");
					}
			    });
			})

		//所有状态为2的格子时间每秒+1
		if(!this.timer){
			this.timer = setInterval(function(){
				$("a[type='andon']").each(function(index){
					var el = $(this);
					if(el.attr("statusECode") == 2){//触发状态
						var oldTouchTimeLong = el.attr("touchTimeLong");
						var newTouchTimeLong = Number(oldTouchTimeLong) + 1000;
						el.attr("touchTimeLong",newTouchTimeLong).find(".touch-time").text(_this.calTime2(newTouchTimeLong));
						if(newTouchTimeLong <= 10000){
							el.css("background","#FFFF00");//黄
						}
						if(newTouchTimeLong > 10000){
							el.css("background","#FF0000");//红
						}
					}
				})
			},1000);
		}	
	},
	loadPage:function(callback){
		var _this = this;
		var promise = app.request({
            url: 'restful/service/execute',
            type:'POST',
            data:{
            	"serviceCode":"ANDON.LIST",
            	"siteCode" : "1310",
            	"workUnitCode" : "SSSS",
            	"andonTypeCode" : ""
        	},
            success: function (data) {
				var andons = $.parseJSON(data.data).data;//data.data,String转JSON
				var newData = _this.group(andons, 6);
				app.tplManager("andonSwiperTpl", newData, "andonSwiperRender");
				var andonSwiper = myApp.swiper('#andonSwiper', {
				    pagination:'#andonSwiperPg'
				});

				_this.event();
            },
            error: function () {
                // myApp.alert("语言列表加载失败");
                alert("error")
            }

        })
	},
	render : function(config){
		
		
	},
	group : function(array, groupLength) {
	    var index = 0;
	    var newArray = {"newAndons":[]};
	    while(index < array.length) {
	        newArray.newAndons.push(array.slice(index, index += groupLength));
	    }
	    return newArray;
	},
	calTime : function(switchOnDateTime){
		//以上是求得 用户输入时间的毫秒数
		var date1=switchOnDateTime;  //开始时间
		// alert("暂停")
		var date2=new Date();    //结束时间
		var date3=date2.getTime()-date1.getTime()  //时间差的毫秒数
		//计算出相差天数
		var days=Math.floor(date3/(24*3600*1000))
		//计算出小时数
		var leave1=date3%(24*3600*1000)    //计算天数后剩余的毫秒数
		var hours=Math.floor(leave1/(3600*1000))
		//计算相差分钟数
		var leave2=leave1%(3600*1000)        //计算小时数后剩余的毫秒数
		var minutes=Math.floor(leave2/(60*1000))
		//计算相差秒数
		var leave3=leave2%(60*1000)      //计算分钟数后剩余的毫秒数
		var seconds=Math.round(leave3/1000)
		// alert(days+"天 "+hours+"小时 "+minutes+" 分钟"+seconds+" 秒")
		return days+"天 "+hours+"小时 "+minutes+" 分钟"+seconds+" 秒";
	},
	calTime2 : function(timeLong){
		//以上是求得 用户输入时间的毫秒数
		//计算出相差天数
		var days=Math.floor(timeLong/(24*3600*1000))
		if(days!=0)
			days=days+"天";
		else
			days="";
		//计算出小时数
		var leave1=timeLong%(24*3600*1000)    //计算天数后剩余的毫秒数
		var hours=Math.floor(leave1/(3600*1000))
		if(hours!=0)
			hours=hours+"小时";
		else
			hours="";
		//计算相差分钟数
		var leave2=leave1%(3600*1000)        //计算小时数后剩余的毫秒数
		var minutes=Math.floor(leave2/(60*1000))
		if(minutes!=0)
			minutes=minutes+"分钟";
		else
			minutes="";
		//计算相差秒数
		var leave3=leave2%(60*1000)      //计算分钟数后剩余的毫秒数
		var seconds=Math.round(leave3/1000)
		if(seconds!=0)
			seconds=seconds+"秒";
		else
			seconds="";
		// alert(days+"天 "+hours+"小时 "+minutes+" 分钟"+seconds+" 秒")
		return days+hours+minutes+seconds;
	}
}

myApp.onPageInit('andonIndex', function (page) {
	/*if($(".view-main div[data-page]").length > 1){
		$(".view-main div[data-page]").eq(0).remove();
	}*/
	$(document).off("keydown");		// 别的页面去除主页的菜单快捷键
	andonIndexView.init();
});



